export default {
  state: {
    positionsUrl: 'https://frontend-test-assignment-api.abz.agency/api/v1/positions',
    userUrl: 'https://frontend-test-assignment-api.abz.agency/api/v1/users?page=1&count=6',
    token: null,
    nextUrl: null,
    showMore: false,
    pageSuccess: null,
    httpStatus: null,
    totalPages: null,
    currentPage: null,
    loading: true,
    userList: []
  },
  mutations: {
    createUserList (state, users) {
      state.nextUrl = users.links.next_url
      state.userList = users.users
      state.pageSuccess = users.success
      state.totalPages = users.total_pages
      state.currentPage = users.page
      if (state.nextUrl !== null) {
        state.showMore = true
      } else {
        state.showMore = false
      }
      state.loading = false
    },
    updateUserList (state, users) {
      state.loading = true
      state.currentPage = users.page
      state.pageSuccess = users.success
      if (state.pageSuccess) {
        state.nextUrl = users.links.next_url
        state.userList = state.userList.concat(users.users)
        if (state.nextUrl !== null) {
          state.showMore = true
        } else {
          state.showMore = false
        }
        if (state.currentPage === state.totalPages) {
          state.showMore = false
        }
      }
      state.loading = false
    },
    httpStatusCode (state, code) {
      state.httpStatus = code
    }
  },
  actions: {
    async fetchUsers ({ commit }) {
      try {
        const userList = await fetch(`${this.state.users.userUrl}`).then(response => response.json())

        commit('createUserList', userList)
      } catch (e) {
        console.log('err', e)
      }
    },
    async registerUser ({ commit, dispatch }, data) {
      try {
        let code = ''
        const token = await fetch('https://frontend-test-assignment-api.abz.agency/api/v1/token'
        ).then(response => response.json())
        await fetch('https://frontend-test-assignment-api.abz.agency/api/v1/users', {
          method: 'POST',
          headers: {
            Token: token.token
          },
          body: data
        }).then(response => {
          console.log(response.json())

          code = response.status
        })
        commit('httpStatusCode', code)
        // обновление списка пользователей
        dispatch('fetchUsers')
      } catch (e) {
        console.log('err', e)
      }
    },
    async showMoreUsers ({ commit, dispatch }) {
      try {
        let userList = null
        if (this.state.users.showMore) {
          userList = await fetch(`${this.state.users.nextUrl}`).then(response => response.json())
        }

        commit('updateUserList', userList)
      } catch (e) {
        console.log('err', e)
      }
    }
  },
  getters: {
    getPositionsUrl: state => {
      return state.positionsUrl
    },
    getUserList: state => {
      return state.userList
    },
    getShowMoreBtn: state => {
      return state.showMore
    },
    getSuccess: state => {
      return state.pageSuccess
    },
    getHttpStatus: state => {
      return state.httpStatus
    },
    getLoading: state => {
      return state.loading
    }
  }
}
