<template>
  <section id="users" class="wow animate__animated animate__fadeIn">
    <div class="container">
      <div class="users-header">
        <h1 class="section-title wow animate__animated animate__fadeInDown" data-wow-duration="1.8s" data-wow-delay="0.5s">Our cheerful users</h1>
        <span class="section-attention wow animate__animated animate__fadeInDown" data-wow-duration="1.8s" data-wow-delay="0.7s">Attention! Sorting users by registration date</span>
      </div>
      <div v-if="getLoading" class="row">
        <div class="col text-center mt-5">
          <Loader />
        </div>
      </div>
      <div v-else class="row wow animate__animated animate__fadeInUp" data-wow-duration="1.8s" data-wow-delay="0.9s">
        <div v-for="user in getUserList" :key="user.id" class="col-md-4 col-sm-6">
          <div class="user-card-container">
            <div class="user-card__photo">
              <img class="user-card__photo-img" :src="user.photo" alt="">
            </div>
            <div
              class="user-card__name"
              :class="{ 'user-card__name-long' : user.name.length > 15 }"
            >{{ user.name }}</div>
            <div class="user-card__desc">{{ user.position }}</div>
            <div class="user-card__email" v-if="user.email.length >= 23" v-b-tooltip :title="user.email">{{ user.email }}</div>
            <div class="user-card__email" v-else>{{ user.email }}</div>
            <div class="user-card__phone">{{ user.phone }}</div>
          </div>
        </div>
        <div class="col-12 text-center" v-if="getSuccess && getShowMoreBtn">
          <button type="button" class="btn btn-primary btn-abz mx-auto" @click="showMoreUsers">Show more</button>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'Users',
  computed: mapGetters(['getUserList', 'getShowMoreBtn', 'getSuccess', 'getLoading']),
  async mounted () {
    this.fetchUsers()
  },
  methods: mapActions(['fetchUsers', 'showMoreUsers'])
}
</script>

<style lang="sass">
  #users
    background-color: #f9f9f3
  .users-header
    margin-bottom: 80px
  .user-card-container
    max-width: 200px
    margin: 0 auto 40px auto
    text-align: center
  .user-card__photo
    margin-bottom: 20px
  .user-card__photo-img
    margin: 0 auto
    display: block
    border-radius: 50%
  .user-card__name
    margin-bottom: 21px
    line-height: 1
    color: #212529
    font-size: 24px
    overflow: hidden
    text-overflow: ellipsis
  .user-card__name-long
    margin-bottom: 17px
  .user-card__desc,
  .user-card__email,
  .user-card__phone
    margin-bottom: 12px
    font-size: 16px
    color: #4c4b4b
  .user-card__email
    white-space: nowrap
    overflow: hidden
    text-overflow: ellipsis

  .tooltip
    .arrow
      display: none
    .tooltip-inner
      max-width: 300px!important

  @media (max-width: 360px)
    .users-header
      margin-bottom: 35px
</style>
