<template>
  <section id="register" class="wow animate__animated animate__fadeIn">
    <div class="container">
      <div class="register-header">
        <h1 class="section-title wow animate__animated animate__fadeInDown" data-wow-duration="1.8s" data-wow-delay="0.5s">Register to get a work</h1>
        <span class="section-attention wow animate__animated animate__fadeInDown" data-wow-duration="1.8s" data-wow-delay="0.7s">Attention! After successful registration and alert, update the <br> list of users in the block from the top</span>
      </div>
      <div class="row justify-content-center wow animate__animated animate__fadeInUp" data-wow-duration="1.8s" data-wow-delay="0.9s">
        <div class="col-md-6">
          <form class="register-form" @submit.prevent="submitHandler">
            <div class="form-group">
              <label for="inputName">Name</label>
              <input
                type="text"
                class="form-control"
                id="inputName"
                placeholder="Your name"
                v-model="name"
                :class="{'is-invalid': ($v.name.$dirty && !$v.name.required) || ($v.name.$dirty && !$v.name.minLength) || ($v.name.$dirty && !$v.name.maxLength)}"
              >
              <div
                v-if="($v.name.$dirty && !$v.name.required)"
                class="invalid-feedback"
              >Введите имя</div>
              <div
                v-else-if="($v.name.$dirty && !$v.name.minLength)"
                class="invalid-feedback"
              >Имя должно быть не меньше {{ $v.name.$params.minLength.min }} символов. Длина имени {{ name.length }} символ(ов)</div>
              <div
                v-else-if="($v.name.$dirty && !$v.name.maxLength)"
                class="invalid-feedback"
              >Имя должно быть не больше {{ $v.name.$params.maxLength.max }} символов. Длина имени {{ name.length }} символ(ов)</div>
            </div>
            <div class="form-group">
              <label for="inputEmail">Email address</label>
              <input
                type="email"
                class="form-control"
                id="inputEmail"
                placeholder="Your email"
                v-model.trim="email"
                :class="{'is-invalid': ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
              >
              <div
                v-if="$v.email.$dirty && !$v.email.required"
                class="invalid-feedback"
              >Поле email не должно быть пустым</div>
              <div
                v-else-if="$v.email.$dirty && !$v.email.email"
                class="invalid-feedback"
              >Введите корректный email</div>
            </div>
            <div class="form-group">
              <label for="inputPhone">Phone number</label>
              <input
                type="text"
                class="form-control"
                id="inputPhone"
                placeholder="+380 XX XXX XX XX"
                @input="acceptNumber"
                v-model="phone"
                :class="{'is-invalid': ($v.phone.$dirty && !$v.phone.required) || ($v.phone.$dirty && !$v.phone.minLength)}"
              >
              <small id="phoneHelp" class="form-text text-muted">Еnter phone number in open format</small>
              <div
                v-if="$v.phone.$dirty && !$v.phone.required"
                class="invalid-feedback"
              >Введите номер телефона</div>
              <div
                v-if="$v.phone.$dirty && !$v.phone.minLength"
                class="invalid-feedback"
              >Номер телефона должен содержать {{ $v.phone.$params.minLength.min - 5 }} цифр. Сейчас введено {{ phone.length - 5}}</div>
            </div>
            <fieldset class="form-group">
              <legend class="col-form-label">Select your position</legend>
              <div v-for="position in positions.positions" :key="position.id" class="form-check">
                <input class="form-check-input" type="radio" name="radios" :id="'radios'+position.id" :value="position.id" :checked="position.id == 1" v-model="positionId">
                <label class="form-check-label" :for="'radios'+position.id">
                  {{ position.name }}
                </label>
              </div>
            </fieldset>
            <div class="form-group custom-file">
              <label class="upload-file-label" for="photoFormControlFile">Photo</label>
              <input
                type="file"
                class="form-control-file custom-file-input"
                id="photoFormControlFile"
                ref="myFile"
                @change="selectedFile"
                accept="image/*"
                :class="{'is-invalid': imageError}"
              >
              <label for="photoFormControlFile" class="custom-file-label">{{ !image.name || image.name == null || fileError ? 'Upload your photo' : image.name }}</label>
              <div v-if="imageError" class="invalid-feedback">
                {{imageError}}
              </div>
            </div>
            <div
              v-if="httpStatus == 401 || httpStatus == 409 || httpStatus == 422"
              class="invalid-feedback text-center d-block mb-3"
            >
              {{ msgAfterRegister }}
            </div>
            <div class="text-center">
              <button type="submit" class="btn btn-primary btn-abz">Sign up now</button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <!-- Modal -->
    <b-modal v-model="showModal" centered>
      <template v-slot:modal-header="{ close }">
        <h5 class="modal-title">Congratulations</h5>
        <!-- Emulate built in modal header close button action -->
        <b-button variant="link" @click="close()">
          <span>&times;</span>
        </b-button>
      </template>
      <template>
        <p class="modal-text">You have successfully passed the registration</p>
      </template>
      <template v-slot:modal-footer="{ ok }">
        <!-- Emulate built in modal footer ok and cancel button actions -->
        <b-button variant="primary" @click="ok()">
          Great
        </b-button>
      </template>
    </b-modal>
  </section>
</template>

<script>
import { email, required, minLength, maxLength } from 'vuelidate/lib/validators'

export default {
  name: 'Register',
  data: () => ({
    name: '',
    email: '',
    phone: '',
    positionId: '1',
    image: {
      size: '',
      height: '',
      width: '',
      name: null
    },
    imageError: '',
    token: null,
    fileError: true,
    positions: [],
    formSuccess: false,
    showModal: false,
    msgAfterRegister: '',
    httpStatus: null
  }),
  validations: {
    name: { required, minLength: minLength(2), maxLength: maxLength(60) },
    email: { email, required },
    phone: { required, minLength: minLength(12 + 5) }
  },
  created () {
    this.loadPositions()
  },
  methods: {
    async loadPositions () {
      this.positions = await fetch(
          `${this.$store.getters.getPositionsUrl}`
      ).then(response => response.json())
    },
    acceptNumber () {
      // Формат ввода номера телефона
      const x = this.phone.replace(/\D/g, '').match(/(\d{0,3})(\d{0,2})(\d{0,3})(\d{0,2})(\d{0,2})/)
      this.phone = !x[2] ? x[1] : '+' + x[1] + ' ' + x[2] + (x[3] ? ' ' + x[3] : '') + (x[4] ? ' ' + x[4] : '') + (x[5] ? ' ' + x[5] : '')
    },
    async submitHandler (e) {
      if (this.$v.$invalid || this.fileError) {
        this.httpStatus = null
        this.showModal = false
        this.$v.$touch()
        if (this.fileError && !this.imageError) {
          this.imageError = 'Image not found'
        }
        return
      }
      this.showModal = false

      const input = document.querySelector('input[type="file"]')
      // console.log(this.fileError)

      const data = new FormData()
      data.append('photo', input.files[0])
      data.append('name', this.name)
      data.append('email', this.email)
      data.append('phone', this.phone.split(' ').join(''))
      data.append('position_id', this.positionId)

      await this.$store.dispatch('registerUser', data)

      this.httpStatus = this.$store.getters.getHttpStatus

      switch (this.httpStatus) {
        case 201:
          this.msgAfterRegister = 'New user successfully registered'
          this.showModal = true
          input.value = ''
          this.image.name = ''
          this.name = ''
          this.email = ''
          this.phone = ''
          this.positionId = '1'
          break
        case 401:
          this.msgAfterRegister = 'The token expired'
          this.showModal = false
          break
        case 409:
          this.msgAfterRegister = 'User with this phone or email already exist'
          this.showModal = false
          break
        case 422:
          this.msgAfterRegister = 'Validation failed'
          this.showModal = false
          break
        default:
          this.showModal = false
          break
      }
    },
    selectedFile () {
      const MAX_SIZE = 5242880
      const MAX_WIDTH = 70
      const MAX_HEIGHT = 70

      let file = this.$refs.myFile.files[0]
      if (file) {
        this.image.name = file.name
      }
      // console.log(file)
      // console.log(file.name)
      if (!file) {
        file = null
        console.log(file)
        this.imageError = 'Image not found'
        this.fileError = true
        return console.log('error Image not found' + '  fileError: ' + this.fileError)
      }
      if (file.type.indexOf('image/jpeg') !== 0) {
        file = null
        this.imageError = 'Extension not supported. Use jpg/jpeg'
        this.fileError = true
        return console.log('Extension not supported. Use jpg/jpeg' + ' 219 fileError: ' + this.fileError)
      }
      this.image.size = file.size
      if (this.image.size > MAX_SIZE) {
        file = null
        this.imageError = `The image size (${this.image.size} MB) is too much (max is ${MAX_SIZE} MB).`
        this.fileError = true
        return console.log('Слишком большой размер файла' + ' 226 fileError: ' + this.fileError)
      }

      const reader = new FileReader()

      reader.readAsDataURL(file)
      reader.onload = evt => {
        const img = new Image()
        img.onload = () => {
          this.image.width = img.width
          this.image.height = img.height
          console.log(this.image)
          this.imageError = ''
          this.fileError = false
          if (this.image.height > MAX_HEIGHT) {
            file = null
            this.imageError = `The image height (${this.image.height}px) is too much (max is ${MAX_HEIGHT}px).`
            this.fileError = true
            return console.log('Слишком большая высота файла' + ' 244 fileError: ' + this.fileError)
          }
          if (this.image.width > MAX_WIDTH) {
            file = null
            this.imageError = `The image width (${this.image.width}px) is too much (max is ${MAX_WIDTH}px).`
            this.fileError = true
            return console.log('Слишком большая ширина файла' + ' 250 fileError: ' + this.fileError)
          }
        }
        img.src = evt.target.result
        this.fileError = false
        // console.log(img.title)
      }
      reader.onerror = evt => {
        console.error(evt)
      }
      this.fileError = false
    }

  }
}
</script>

<style lang="sass">
  .register-header
    margin-bottom: 30px
  .register-form
    .col-form-label
      margin-bottom: 20px
    .form-check
      padding-left: 0
      margin-bottom: 7px
  .custom-file
    margin-top: 20px
    margin-bottom: 40px
  .upload-file-label
    display: block
    margin-top: -2rem
  .form-control-file
    height: calc(1.5em + 0.75rem + 2px)
    padding: 0.375rem 0.75rem
    font-weight: 400
    line-height: 1.5
    color: #495057
    background-color: #fff
    border: 1px solid #ced4da
    border-radius: 0.25rem
    content: ""
</style>
