<template>
  <header class="header animate__animated animate__fadeInDown" ref="header" @scroll="handleScroll">
    <div class="container">
      <div class="header-container">
        <div class="header__logo">
          <a class="header__logo-link" href="#" @click="handleLink">
            <img src="../../assets/img/logo.svg" alt="">
          </a>
        </div>
        <div class="header__menu">
          <Sidebar>
            <nav class="menu">
              <ul class="menu-list">
                <li v-for="(n, i) in li" :key="i"><a class="menu-list-item" :class="{ active: active === i }" :href="n.link" @click="handleLink(i)">{{ n.name }}</a></li>
              </ul>
            </nav>
          </Sidebar>
        </div>
        <Burger></Burger>
      </div>
    </div>
  </header>
</template>

<script>
import { mutations } from '../../store/index.js'
import Sidebar from './Sidebar'
import Burger from './Burger'

export default {
  name: 'Menu',
  data: () => ({
    width: null,
    active: null,
    li: [
      {
        name: 'About',
        link: '#about'
      },
      {
        name: 'Relationships',
        link: '#register'
      },
      {
        name: 'Requirements',
        link: '#register'
      },
      {
        name: 'Users',
        link: '#users'
      },
      {
        name: 'Sign Up',
        link: '#register'
      }
    ]
  }),
  components: {
    Sidebar, Burger
  },
  methods: {
    updateWidth () {
      this.width = window.innerWidth
      if (this.width < 769) {
        mutations.mobileWindow()
      }
    },
    handleLink (index) {
      if (this.width < 769) {
        mutations.toggleNav()
      }
      this.active = index
    },
    handleScroll (event) {
      const header = this.$refs.header
      if (window.scrollY > 60) {
        header.setAttribute(
          'style',
          'box-shadow: 0 5px 5px rgba(0,0,0,0.2)'
        )
      } else if (window.scrollY < 60) {
        header.setAttribute(
          'style',
          'box-shadow: none'
        )
      }
    }
  },
  created () {
    window.addEventListener('resize', this.updateWidth)
    this.updateWidth()

    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="sass">
  .header
    margin-top: -60px
    position: fixed
    background-color: #ffffff
    z-index: 999
    width: 100%
    line-height: 1
  .header-container
    min-height: 60px
    display: flex
    justify-content: space-between
    flex-direction: row
    align-items: center
  .header__logo
    position: relative
  .header__logo-link
    img
      position: absolute
      top: -2px
      left: 0
      z-index: 9999

  .menu-list
    li
      display: block
      float: left
  .menu-list-item
    color: #212529
    font-size: 14px
    margin-left: 30px
  .menu-list-item.active
    color: #1b4a8a

  @media (max-width: 768px)
    .menu-list
      margin-top: 60px
      padding: 25px 15px
      border-top: 1px solid #e1e1e1
      li
        float: none
        margin-bottom: 20px
      .menu-list-item
        margin-left: 0
        font-size: 16px

  @media (max-width: 360px)
    header
      margin-top: -40px
    .header-container
      min-height: 40px
    .menu-list
      margin-top: 40px
</style>
